body {
  position: relative;
  min-height: 100vh;
  padding-bottom: 102px;
}

a {
  text-decoration: none;
}
ul {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  padding: 0;
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  margin: 0 auto;
}

html {
  scroll-behavior: smooth;
}

body {
  &.modal-open {
    overflow: hidden;
  }
}
