.backdrop.footer__backdrop {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.8);

  opacity: 1;
  transition: opacity 500ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 500ms cubic-bezier(0.4, 0, 0.2, 1);
  @media screen and(max-width: 767px) {
    align-items: center;
  }

  &.is-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;

    .team-modal {
      transform: scale(1.5);
    }
  }
}

.footer__backdrop {
  z-index: 1000;
}

.team-modal {
  z-index: 1000;
  width: 300px;
  height: 430px;
  background-color: var(--background-main);
  padding-top: 15px;
  border-radius: 10px;

  background-image: url('../images/teamModal-bg.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  transform: scale(1);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  }

  &::-webkit-scrollbar-thumb {
    background-color: rgba(253, 254, 255, 0.2);
    outline: none;
    border-radius: 10px;
  }
  @media screen and(min-width: 768px) {
    width: 600px;
    height: 500px;
    // overflow-y: scroll;
  }
  @media screen and(min-width:1200px) {
    width: 800px;
    height: 500px;
    // overflow-y: scroll;
  }
  @media screen and(max-height:430px) {
    height: 90vh;
    overflow-y: scroll;
  }
}

body.modal-open {
  overflow: hidden;
}

.glide__slide {
  border: none;
  width: 0.9vh;
  @media screen and(min-width:768px) {
    width: 350px;
    background-position: 134% 62%;
  }
}
.footer__sociallink {
  margin-bottom: 15px;
  padding-left: 10px;
  &:first-child {
    padding-left: 0px;
  }
}

.team-modal__card {
  padding-left: 30px;
  padding-right: 30px;
  margin-top: auto;
  margin-bottom: auto;
}

.fa-xl {
  color: var(--text-footer);

  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    transform: scale(1.2);
    color: var(--text-dark);
  }
}

.footer__social {
  display: flex;
}

.footermodalbtn-close {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border: 1px solid;
  border-color: #000;
  color: #000;
  top: 0;
  right: 0;
  background-color: transparent;
  border-radius: 50%;
  margin-top: 8px;
  margin-right: 8px;
  transform: scale(1);
  transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
    color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    transform: scale(1.2);
    border-color: var(--accent);
    color: var(--accent);
  }

  &__icon-close {
    stroke: #000;
    transition: stroke 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  &:hover &__icon-close,
  &:focus &__icon-close {
    stroke: var(--accent);
  }
}

.student__card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.student-foto {
  width: 150px;
  height: auto;

  border-radius: 50%;
}

.border__outside {
  padding: 7px;
  margin: 0 auto;
  width: 170px;
  border-radius: 50%;
  border-bottom: 2px solid var(--text-footer);
  border-right: 2px solid var(--text-footer);
  margin-bottom: 10px;
  @media screen and(min-width:768px) {
    margin-bottom: 15px;
  }
}

.border__inside {
  padding: 7px;
  border-radius: 50%;
  border: 1.5px solid var(--text-footer);
}

.student__name {
  font-family: 'Roboto';
  padding-bottom: 8px;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.15;
  letter-spacing: 0.04em;

  color: var(--text-footer);
  margin-bottom: 10px;
  border-bottom: 2px solid #99a1ca;

  @media screen and(min-width:768px) {
    font-size: 25px;
    margin-bottom: 15px;
  }
}

.student__role {
  font-family: 'Roboto';
  font-size: 18px;
  font-weight: 400;
  line-height: 1.15;
  letter-spacing: 0.04em;

  color: var(--text-light);
  margin-bottom: 10px;
  @media screen and(min-width:768px) {
    font-size: 25px;
    margin-bottom: 15px;
  }
}

.student__about {
  font-family: 'Roboto';
  font-size: 14px;
  font-weight: 400;
  line-height: 1.15;
  letter-spacing: 0.04em;

  color: var(--text-footer);
  margin-bottom: 10px;
  text-align: center;
  @media screen and(min-width:768px) {
    font-size: 20px;
    margin-top: 25px;
  }
}

.glide__slides--footer {
  display: flex;
}

.arrow__footer {
  color: var(--text-footer);
}
